import Vue from 'vue'
import Vuex from 'vuex'
import initData from '../test.json'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    idCount: 12,
    itemCount: 12,
    showingListIndex: 0,
    userInfo: {
      username: 'JerryChan',
      prfImg: '../assets/logo.png'
    },
    lists: []
  },
  mutations: {
    initStateFromLocalStorage (state) {
      let temp = localStorage.getItem('state')
      if (temp) {
        this.replaceState(Object.assign(state, JSON.parse(localStorage.getItem('state'))))
      } else {
        this.replaceState(Object.assign(state, initData))
      }
    },
    itemDone (state, id) {
      this.getters.getShowingItemById(id).value = true;
    },
    itemCancelDone (state, id) {
      this.getters.getShowingItemById(id).value = false;
    },
    addItem (state, newItem) {
      this.getters.getShowingList.listItems.push(newItem)
      state.itemCount++
      state.idCount++
    },
    changeShowingList (state, idx) {
      state.showingListIndex = idx
    },
    toggleItemStar (state, id) {
      let temp = this.getters.getShowingItemById(id)
      temp.isStarred = !temp.isStarred
    },
    createList (state) {
      state.lists.push({
        listName: 'New list',
        listItems: []
      })
    },
    editItem (state, payload) {
      let temp = this.getters.getShowingItemById(payload.id);
      temp.deadline = payload.deadline;
      temp.title = payload.title;
    }
  },
  getters: {
    getShowingList: (state) => {
      return state.lists[state.showingListIndex]
    },
    getShowingItemById: (state, getters) => (id) => {
      return getters.getShowingList.listItems.find(todo => todo.itemId === id);
    }
  }
})
