<!-- To be extended. Now it can just only show the username and profile image.-->
<template>
  <div class="profile-bar">
    <div class="profile">
      <img alt="profile_img" src="../assets/logo.png">
      <label>{{ username }}</label>
      <v-icon name="angle-down" />
    </div>
    <span>
      <v-icon name="regular/bell" class="bell"/>
      <v-icon name="regular/comment" class="comment"/>
    </span>
  </div>
</template>

<script>
export default {
  props: {
    username: String
  }
}
</script>

<style lang="less" scoped>
@hover-color: #e5f3ff;
@big-icon-width: .875rem /* 14/16 */;
@big-icon-height: 1rem /* 16/16 */;
@bar-height: 2.5rem /* 40/16 */;
@profile-img-size: 1.6875rem /* 27/16 */;
@profile-img-border: .0625rem /* 1/16 */ none gray;
@profile-img-margin: .39rem /* 6/16 */;
@profile-icon-padding: .76rem /* 2/16 */; 

.profile-bar {
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: .8125rem /* 13/16 */;
  height: @bar-height;
  .profile {
    display: inherit;
    align-items: center;
    img {
      width: @profile-img-size;
      height: @profile-img-size;
      border: @profile-img-border;
      border-radius: 50%;
      margin: @profile-img-margin;
    }
    &:hover {
      background-color: @hover-color;
    }
  }
  span{
    display: flex;
  }
  .fa-icon {
    width: @big-icon-width;
    height: @big-icon-height;
    padding: @profile-icon-padding;
    &:hover {
      background-color: @hover-color;
    }
  }
}

</style>
