<!-- A form to modify deadline and title of a todo item. -->
<template>
  <div class="form">
    <div>
      <v-icon name="edit"></v-icon>
      <span> Title</span>
    </div><div>
      <input type="text" v-model="myTitle" class="title">
    </div>
    <div>
      <v-icon name="edit"></v-icon>
      <span> Deadline (YYYY/MM/DD)</span>
    </div>
    <date-picker v-model="myDeadline"></date-picker>
    <div class="button-area">
      <button class="confirm" @click="commitEdit">确认</button>
      <button class="cancel" @click="closeWindow">取消</button>
    </div>
  </div>
</template>

<script>
import datePicker from 'vue-date'
export default {
  methods: {
    closeWindow: function(e) {
      this.$emit("close");
    },
    commitEdit: function() {
      this.$store.commit("editItem", {title: this.myTitle, deadline: this.myDeadline,id: this.id});
      this.$emit("close");
    }
  },
  components: {
    datePicker
  },
  props: {
    title: String,
    deadline: String,
    id: Number
  },
  data: function () {
    return {
      myTitle: this.title,
      myDeadline: this.deadline
    }
  }
}
</script>

<style lang="less" scoped>
@window-margin: 1.875rem /* 30/16 */ 1.875rem /* 30/16 */ 1.875rem /* 30/16 */ 1.875rem /* 30/16 */;
@button-color-red: #71AF8C;
@button-color-blue:  rgb(28, 120, 224);
@button-padding: .375rem /* 6/16 */ .8125rem /* 13/16 */ .375rem /* 6/16 */ .8125rem /* 13/16 */;
@space-between: .375rem /* 6/16 */;

.form{
  width: 14.375rem /* 230/16 */;
  color: grey;
  font-size: .8125rem /* 13/16 */;
  margin: @window-margin;
  div {
    margin-top: .8125rem /* 13/16 */;
    text-align: center;
  }
  .button-area {
    margin-top: .8125rem /* 13/16 */;
    text-align: center;
  }
}

.title {
  border: 0;
  border-bottom: 1px /* 1/16 */ solid black;
  background-color: transparent;
  outline: none;
  text-align: center;
  width: 100%;
}

button {
  border-radius: 5px;
  border: none;
  cursor: pointer;
  color: white;
  padding: @button-padding;
  margin: @space-between;
  &.confirm {
    background-color: @button-color-red;
  }
  &.cancel {
    background-color: @button-color-blue;
  }
}
</style>
