<!-- A column contain profile information, all the list, and search module-->
<template>
  <div class="leftColumn">
    <search-bar @expand="toggleExpand"></search-bar>
    <profile-bar :username="username"></profile-bar>
    <transition-group name="expand" tag="div" class="expansion-part">
    <div class="expansion-part" v-if="isExpand" key="expansion-part">
      <div class="list-space">
        <div  class="list" :class="{isSelected: index==showingListIndex}" v-for="(list, index) in list" :key="index" @click="clickList(index)">
          <v-icon v-if="index==0" name="inbox" class="inbox"/>
          <v-icon v-if="index!=0" name="list-ul" class="selfList"/>
          <span>{{ list.listName }}</span>
        </div>
      </div>
      <div class="create-list" @click="createList">
        <v-icon name="plus"></v-icon>
        <span>Creat list</span>
      </div>
    </div>
    </transition-group>
  </div>
</template>

<script>
import SearchBar from './SearchBar.vue'
import ProfileBar from './ProfileBar.vue'
export default {
  components: {
    SearchBar,
    ProfileBar
  },
  computed: {
    // fetch info from vuex.
    username () {
      return this.$store.state.userInfo.username
    },
    prfImg () {
      return this.$store.state.userInfo.prfImg
    },
    list () {
      return this.$store.state.lists
    },
    showingListIndex () {
      return this.$store.state.showingListIndex
    }
  },
  methods: {
    // change showing list by clicking the list name
    clickList (idx) {
      this.$store.commit('changeShowingList', idx)
    },
    // create a new empty list.
    createList () {
      this.$store.commit('createList')
    },
    // issue: change when folded
    toggleExpand () {
      if (this.screenWidth <= 768) {
        this.isExpand = !this.isExpand 
      } else {
        this.isExpand = true;
      }
    },
    getScreenWidth () {
      this.screenWidth = document.documentElement.clientWidth
    },
    resizeListener (event) {
      this.getScreenWidth();
      if (this.screenWidth >= 768) {
        this.isExpand = true;
      }
    }
  },
  data: function() {
    return {
      isExpand: true,
      screenWidth: 0
    }
  },
  mounted () {
    this.$nextTick(function() {
      window.addEventListener('resize', this.resizeListener)
      
    });
    this.resizeListener();
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.resizeListener)
  }
}
</script>

<style lang="less" scoped>
@left-column-width: 30rem /* 233/16 */;
@left-column-width1: 14.5625rem /* 233/16 */;
@big-icon-width: .75rem /* 12/16 */;
@big-icon-height: 1rem /* 16/16 */;
@big-icon-padding: .6875rem /* 11/16 */ .75rem /* 12/16 */ .6875rem /* 11/16 */ .75rem /* 12/16 */;
@bar-height: 2.5rem /* 40/16 */;
@hover-color: #e5f3ff;
@selected-color: #cce8ff;
@inbox-color: #2b88d9;

.autofill {
  flex: 1 1;
}

.leftColumn {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  font-size: .75rem /* 12/16 */;
  user-select: none;
  background-color: white;
  
}

.fa-icon {
  width: @big-icon-width;
  height: @big-icon-height;
  padding: @big-icon-padding;
  &.inbox {
    color: @inbox-color;
  }
}

.list {
  display: flex;
  background-color: white;
  height: @bar-height;
  align-items: center;
  &:hover {
    background-color: @hover-color;
  }
  &.isSelected {
    background-color: @selected-color;
  }
}

.create-list {
  display: flex;
  align-items: center;
  color: @inbox-color;
}

.expand-enter-active, .expand-move {
  transition: all 0.5s;
}



.expand-enter{
  opacity: 0;
  transform: translateY(-3.125rem /* 50/16 */);
}

</style>
