import Vue from 'vue'
import VueRouter from 'vue-router'

import TheMainFrame from '../components/TheMainFrame.vue'
import LoginPage from '../components/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    component: LoginPage
  },
  {
    path: '/list',
    components: {
      default: TheMainFrame,
    }
  },
  {
    path: '/',
    redirect: '/list'
  }
]

const router = new VueRouter({
  routes
})

export default router
