<!-- a List contain todo items and completed items. -->
<template>
  <div class="baselist">
    <div class="list-name">
      {{ showingList.listName }}
      <div class="sort-choices">
        <button class="sort-btn" @click="resort(0)">Due↑↓</button>
        <button class="sort-btn" @click="resort(1)">Title↑↓</button>
      </div>
    </div>
    <div class="list-body">
      <add-bar></add-bar>
      <transition-group name="list">
        <base-item 
         v-for="(todo, index) in todoList" 
         v-bind="todo" 
         :key="todo.itemId" 
         @change="itemDone(index)">
        </base-item>
      <div key="togglebutton">
        <label 
         class="notshowing"
         v-show="!isCompletedShown"
         v-on:click="toggleShowCompleted(true)"
        > {{ completedList.length }} COMPLETED TO-DOS </label>
        <label 
         class="showing" 
         v-show="isCompletedShown" 
         v-on:click="toggleShowCompleted(false)"
        > HIDE COMPLETED TO-DOS </label>
      </div>
        <base-item 
         v-show="isCompletedShown" 
         class="completed-item" 
         v-for="(todo, index) in completedList" 
         v-bind="todo" 
         :key="todo.itemId" 
         v-on:change="itemCancelDone(index)"
        ></base-item>     
      </transition-group>
    </div>
  </div>
</template>

<script>
import BaseItem from './BaseItem.vue'
import AddBar from './AddBar.vue'
import {mapGetters} from 'vuex'
export default {
  name: 'BaseList',
  data: function () {
    return {
      // isCompletedShown: Whether show the completed list.
      isCompletedShown: false,
      sortFuncArr: [this.sortItemByDeadline, this.sortItemByName],
      sortFuncIdx: 0,
      reverseSort: false
    }
  },
  computed: {
    showingList () {
      return this.$store.getters.getShowingList
    },
    todoList () {
      return this.showingList.listItems.filter(todo => todo.value === false).slice().sort((a, b)=>this.sortFunction(a, b, this.sortFuncArr[this.sortFuncIdx]));
    },
    completedList () {
      return this.showingList.listItems.filter(todo => todo.value === true).slice().sort((a, b)=>this.sortFunction(a, b, this.sortFuncArr[this.sortFuncIdx]));
    }
  },
  components: {
    BaseItem,
    AddBar
  },
  methods: {
    toggleShowCompleted: function (status) {
      this.isCompletedShown = status
    },
    sortItemByDeadline: function (a, b) {
      let dateA = new Date(a.deadline)
      let dateB = new Date(b.deadline)
      return this.reverseSort ? dateA - dateB : dateB - dateA;
    },
    sortItemByName: function (a, b) {
      const res = a.title.localeCompare(b.title);
      return this.reverseSort ? res : -res;
    },
    sortFunction (a, b, sortBy = this.sortItemByDeadline) {
      const res = sortBy(a, b);
      if (res === 0) return a.itemId - b.itemId;
      return res;
    },
    resort (func) {
      this.reverseSort = this.sortFuncIdx === func ? !this.reverseSort : false;
      this.sortFuncIdx = func;
    }
  }
}
</script>

<style lang="less" scoped>

@togglebutton-font-size: .625rem /* 10/16 */;
@togglebutton-height: 1.5625rem /* 25/16 */;
@togglebutton-padding: 0 .625rem /* 10/16 */ 0 .625rem /* 10/16 */;
@togglebutton-margin: 1.25rem /* 20/16 */ 0 .1875rem /* 3/16 */ 0;
@togglebutton-not-selected: #71AF8C;
@togglebutton-selected: #5F8D73;
@completeditem-color: #a9a9a9;
@base-list-margin:.75rem /* 12/16 */ .75rem /* 12/16 */ .75rem /* 12/16 */ .75rem /* 12/16 */;
@text-size: .75rem /* 12/16 */;
@name-bar-height: 2.3rem /* 56/16 */;
@name-bar-padding: 0 0 0 .75rem /* 12/16 */;

label {
  display: inline-flex;
  font-size: @togglebutton-font-size;
  height: @togglebutton-height;
  color: white;
  margin: @togglebutton-margin;
  padding: @togglebutton-padding;
  align-items: center;
  user-select: none;
  &.notshowing {
    background-color: @togglebutton-not-selected;
    &:hover {
      background-color: @togglebutton-selected;
    }
  }
  &.showing {
    background-color: @togglebutton-selected;
  }
}
.completed-item {
  color: @completeditem-color !important;
}
.baselist {
  flex: 1 1 auto;
}
.list-body {
  margin: @base-list-margin;
}

.list-name {
  user-select: none;
  display: flex;
  align-items: center;
  background-color: @togglebutton-not-selected;
  color: white;
  font-size: 1.25rem /* 20/16 */;
  height: @name-bar-height;
  padding: @name-bar-padding;
  .sort-choices {
    height: 100%;
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
  }
  .sort-btn {
    width: 3rem;
    height: 100%;
    border: 0;
    background-color: inherit;
    color: inherit;
    font-size: 0.125rem;
    user-select: none;
    outline: none;
    &:hover {
      background-color: @togglebutton-selected;
    }
  }
}
// to be rewritten
.list-enter-active, .list-leave-active, .list-move {
  transition: all 0.5s;
}
.list-enter {
  opacity: 0;
  transform: translateY(3.125rem /* 50/16 */);
}
.list-leave-to {
  display: none;
}


</style>
